'use client';

import { useState, useEffect } from 'react';
import { Eye, X, Check, Clock, XCircle, Search, Filter, Calendar, User, Phone, Mail, ChevronDown } from 'lucide-react';

type Reservation = {
  id: number;
  servicio_id: number;
  nombre_completo: string;
  email: string;
  telefono: string;
  fecha_evento: string;
  hora_preferida: string;
  notas: string;
  estado: 'pendiente' | 'confirmada' | 'cancelada' | 'completada';
  fecha_solicitud: string;
  servicio_nombre?: string;
};

const statusLabels: Record<string, string> = {
  pendiente: 'Pendiente',
  confirmada: 'Confirmada',
  cancelada: 'Cancelada',
  completada: 'Completada',
};

const statusColors: Record<string, string> = {
  pendiente: 'bg-yellow-100 text-yellow-800',
  confirmada: 'bg-green-100 text-green-800',
  cancelada: 'bg-red-100 text-red-800',
  completada: 'bg-blue-100 text-blue-800',
};

export default function ReservationsPage() {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedReservation, setSelectedReservation] = useState<Reservation | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [isUpdating, setIsUpdating] = useState(false);

  useEffect(() => {
    fetchReservations();
  }, []);

  const fetchReservations = async () => {
    try {
      const response = await fetch('/api/admin/reservations');
      const data = await response.json();
      if (data.success) {
        setReservations(data.data);
      }
    } catch (error) {
      console.error('Error al cargar reservaciones:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const updateStatus = async (id: number, newStatus: string) => {
    setIsUpdating(true);
    try {
      const response = await fetch(`/api/admin/reservations/${id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ estado: newStatus }),
      });

      if (response.ok) {
        setReservations(reservations.map(r => r.id === id ? { ...r, estado: newStatus as Reservation['estado'] } : r));
        if (selectedReservation?.id === id) {
          setSelectedReservation({ ...selectedReservation, estado: newStatus as Reservation['estado'] });
        }
      }
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setIsUpdating(false);
    }
  };

  const filteredReservations = reservations.filter((r) => {
    const matchesSearch = r.nombre_completo.toLowerCase().includes(searchTerm.toLowerCase()) ||
                          r.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
                          r.telefono.includes(searchTerm);
    const matchesStatus = statusFilter === 'all' || r.estado === statusFilter;
    return matchesSearch && matchesStatus;
  });

  const pendingCount = reservations.filter(r => r.estado === 'pendiente').length;

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando reservaciones...</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
        <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Reservaciones</h1>
            <p className="text-gray-600 mt-1">
              {pendingCount > 0 && <span className="text-yellow-600 font-medium">{pendingCount} pendiente{pendingCount > 1 ? 's' : ''}</span>}
              {pendingCount === 0 && 'Sin reservaciones pendientes'}
            </p>
          </div>

          <div className="flex flex-col sm:flex-row gap-3">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
              <input
                type="text"
                placeholder="Buscar por nombre, email o teléfono..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent w-full sm:w-64"
              />
            </div>
            <div className="relative">
              <Filter className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                className="pl-10 pr-10 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent appearance-none bg-white w-full sm:w-44"
              >
                <option value="all">Todos los estados</option>
                <option value="pendiente">Pendientes</option>
                <option value="confirmada">Confirmadas</option>
                <option value="completada">Completadas</option>
                <option value="cancelada">Canceladas</option>
              </select>
              <ChevronDown className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 pointer-events-none" />
            </div>
          </div>
        </div>
      </div>

      {filteredReservations.length > 0 ? (
        <div className="bg-white rounded-lg shadow-sm overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Solicitante</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Servicio</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Fecha Evento</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Estado</th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Solicitado</th>
                  <th className="px-6 py-4 text-right text-sm font-semibold text-gray-900">Acciones</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredReservations.map((reservation) => (
                  <tr key={reservation.id} className="hover:bg-gray-50 transition-colors">
                    <td className="px-6 py-4">
                      <div>
                        <p className="font-medium text-gray-900">{reservation.nombre_completo}</p>
                        <p className="text-sm text-gray-500">{reservation.email}</p>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-gray-600">{reservation.servicio_nombre || '-'}</td>
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-2 text-gray-600">
                        <Calendar className="w-4 h-4" />
                        {new Date(reservation.fecha_evento).toLocaleDateString('es-PE')}
                        {reservation.hora_preferida && <span className="text-gray-400">• {reservation.hora_preferida}</span>}
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <span className={`inline-flex px-3 py-1 rounded-full text-xs font-medium ${statusColors[reservation.estado]}`}>
                        {statusLabels[reservation.estado]}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-500">
                      {new Date(reservation.fecha_solicitud).toLocaleDateString('es-PE')}
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center justify-end gap-2">
                        <button
                          onClick={() => setSelectedReservation(reservation)}
                          className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg transition-colors"
                          title="Ver detalles"
                        >
                          <Eye className="w-5 h-5" />
                        </button>
                        {reservation.estado === 'pendiente' && (
                          <>
                            <button
                              onClick={() => updateStatus(reservation.id, 'confirmada')}
                              className="p-2 text-green-600 hover:bg-green-50 rounded-lg transition-colors"
                              title="Confirmar"
                            >
                              <Check className="w-5 h-5" />
                            </button>
                            <button
                              onClick={() => updateStatus(reservation.id, 'cancelada')}
                              className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                              title="Cancelar"
                            >
                              <XCircle className="w-5 h-5" />
                            </button>
                          </>
                        )}
                        {reservation.estado === 'confirmada' && (
                          <button
                            onClick={() => updateStatus(reservation.id, 'completada')}
                            className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg transition-colors"
                            title="Marcar como completada"
                          >
                            <Check className="w-5 h-5" />
                          </button>
                        )}
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      ) : (
        <div className="bg-white rounded-lg shadow-sm p-12 text-center">
          <Calendar className="w-16 h-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 mb-2">No hay reservaciones</h3>
          <p className="text-gray-600">
            {searchTerm || statusFilter !== 'all' ? 'No se encontraron reservaciones con los filtros aplicados' : 'Aún no hay reservaciones registradas'}
          </p>
        </div>
      )}

      {/* Detail Modal */}
      {selectedReservation && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-lg w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-gray-900">Detalle de Reservación</h2>
                <button onClick={() => setSelectedReservation(null)} className="p-2 text-gray-400 hover:text-gray-600 transition-colors">
                  <X className="w-6 h-6" />
                </button>
              </div>
            </div>

            <div className="p-6 space-y-6">
              <div className="flex items-center justify-between">
                <span className={`inline-flex px-3 py-1 rounded-full text-sm font-medium ${statusColors[selectedReservation.estado]}`}>
                  {statusLabels[selectedReservation.estado]}
                </span>
                <span className="text-sm text-gray-500">
                  ID: #{selectedReservation.id}
                </span>
              </div>

              <div className="space-y-4">
                <div>
                  <h3 className="text-sm font-medium text-gray-500 mb-2">Información del Solicitante</h3>
                  <div className="bg-gray-50 rounded-lg p-4 space-y-2">
                    <div className="flex items-center gap-2">
                      <User className="w-4 h-4 text-gray-400" />
                      <span>{selectedReservation.nombre_completo}</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Mail className="w-4 h-4 text-gray-400" />
                      <a href={`mailto:${selectedReservation.email}`} className="text-blue-600 hover:underline">{selectedReservation.email}</a>
                    </div>
                    <div className="flex items-center gap-2">
                      <Phone className="w-4 h-4 text-gray-400" />
                      <a href={`tel:${selectedReservation.telefono}`} className="text-blue-600 hover:underline">{selectedReservation.telefono}</a>
                    </div>
                  </div>
                </div>

                <div>
                  <h3 className="text-sm font-medium text-gray-500 mb-2">Detalles del Evento</h3>
                  <div className="bg-gray-50 rounded-lg p-4 space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-gray-600">Servicio:</span>
                      <span className="font-medium">{selectedReservation.servicio_nombre || '-'}</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-gray-600">Fecha del evento:</span>
                      <span className="font-medium">{new Date(selectedReservation.fecha_evento).toLocaleDateString('es-PE', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}</span>
                    </div>
                    {selectedReservation.hora_preferida && (
                      <div className="flex items-center justify-between">
                        <span className="text-gray-600">Hora preferida:</span>
                        <span className="font-medium">{selectedReservation.hora_preferida}</span>
                      </div>
                    )}
                    <div className="flex items-center justify-between">
                      <span className="text-gray-600">Solicitado:</span>
                      <span className="font-medium">{new Date(selectedReservation.fecha_solicitud).toLocaleDateString('es-PE')}</span>
                    </div>
                  </div>
                </div>

                {selectedReservation.notas && (
                  <div>
                    <h3 className="text-sm font-medium text-gray-500 mb-2">Notas</h3>
                    <div className="bg-gray-50 rounded-lg p-4">
                      <p className="text-gray-700 whitespace-pre-wrap">{selectedReservation.notas}</p>
                    </div>
                  </div>
                )}
              </div>
            </div>

            <div className="p-6 border-t border-gray-200 bg-gray-50">
              <div className="flex flex-wrap gap-2">
                {selectedReservation.estado === 'pendiente' && (
                  <>
                    <button
                      onClick={() => updateStatus(selectedReservation.id, 'confirmada')}
                      disabled={isUpdating}
                      className="flex-1 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors disabled:opacity-50 flex items-center justify-center gap-2"
                    >
                      <Check className="w-4 h-4" />
                      Confirmar
                    </button>
                    <button
                      onClick={() => updateStatus(selectedReservation.id, 'cancelada')}
                      disabled={isUpdating}
                      className="flex-1 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors disabled:opacity-50 flex items-center justify-center gap-2"
                    >
                      <XCircle className="w-4 h-4" />
                      Cancelar
                    </button>
                  </>
                )}
                {selectedReservation.estado === 'confirmada' && (
                  <button
                    onClick={() => updateStatus(selectedReservation.id, 'completada')}
                    disabled={isUpdating}
                    className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 flex items-center justify-center gap-2"
                  >
                    <Check className="w-4 h-4" />
                    Marcar como Completada
                  </button>
                )}
                <button
                  onClick={() => setSelectedReservation(null)}
                  className="flex-1 px-4 py-2 text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  Cerrar
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
